.strategic-life-area-raters {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  padding: 20px;
  max-width: 1200px; /* Adjust the width based on your preferred maximum width */
  margin: 0 auto; /* This will center the grid if it's smaller than the max-width */
}

.slu-plot-container {
  display: flex;
  justify-content: center;
}

.sla-rater {
  background: #f3f4f6;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.slu-rater {
  border-bottom: 1px solid #e1e4e8;
  padding: 10px 0;
}

.slu-rater:last-child {
  border-bottom: none;
}

.slu-rater h3 {
  font-size: 0.8rem;
  margin: 0 0 10px 0;
}

.slu-rater label {
  display: block;
  margin-bottom: 5px;
}

.slu-rater input[type='number'] {
  width: 70px;
  margin-right: 10px;
  padding: 5px;
  border: 1px solid #d1d5da;
  border-radius: 4px;
}

/* Label styling */
.slu-rater label {
  font-weight: 500;
  margin-right: 10px;
}

.article-container {
  max-width: 1200px;
}